body{
  background-color: grey;
  background-image: url(../public/images/background.jpeg);
  background-size:contain;
  background-position: center;
}
/* Text containers for each page */

.text-area{
  padding-top: 15%;
  border:1px solid black;
  display: flex;
  width: 70vw;
  margin: auto;
  border-radius: 5px;
  opacity: 0.9;
  background-color: #6C757D;
}


/* Home buttons */
#home-buttons{
  background-color: grey;
}
a{
  text-decoration: none;
  color: black;
}

/* nav */
.navbar {
  background-color: black;
  color: white;
}

/* Card images on home screen */
#cardImage{
  width: 15rem;
  text-align: center;
}
.card_display{
  padding-top: 10%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  /* opacity: 0.9; */
}
.card_area{
  display: flex;  
  gap: 2%;  
  flex-direction: row;  
  flex-wrap: wrap;  
  align-items: center;
  justify-content: center;
}

@media screen and (max-width: 824px) {
body{
background-size: cover;
}
.text-area{
  margin-top: 30%;   
}

/* Card images on home screen */
#cardImage{
  width: 15rem;
  display: flex;
  flex-direction: row;
}
.card_display{
  padding-top: 30%;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  background-color: #6C757D;
  opacity: 0.7;
}

}